import state from './state';
import interfaceTemplate from './interface';
import PrintUi from './PrintUi';
// eslint-disable-next-line import/no-cycle
import { renderFailure, renderCategoryMenu, renderSuccess } from './render';

export const elementBySelector = (element, selector) => element.querySelector(selector);

export const setActive = (element, activeSelectorClass) => {
  element.classList.add(activeSelectorClass);
};

export const removeActive = (element, activeSelectorClass) => {
  element.classList.remove(activeSelectorClass);
};

export const playAudio = (element) => {
  const toPlay = element.querySelector('audio');
  toPlay.play();
};

const playSelector = (selector) => {
  const toPlay = document.querySelector(selector);
  toPlay.play();
};

export const closeMenu = () => {
  const menuElement = document.querySelector('.sidepanel');
  menuElement.setAttribute('style', 'width: 0px');
};

export const showButton = (selector) => {
  const buttonToShow = document.querySelector(selector);
  buttonToShow.classList.remove('hidden_opacity');
};

export const hideButton = (selector) => {
  const buttonToHide = document.querySelector(selector);
  buttonToHide.classList.add('hidden_opacity');
};

const activeArray = () => document.querySelectorAll('.active_card');

const domArray = (selector) => document.querySelectorAll(selector);

const addFullStar = () => {
  const fullStar = new PrintUi(interfaceTemplate.fullStar);
  const scoreArea = document.querySelector('.score');
  fullStar.printHTML(scoreArea);
};

const addEmptyStar = () => {
  const fullStar = new PrintUi(interfaceTemplate.emptyStar);
  const scoreArea = document.querySelector('.score');
  fullStar.printHTML(scoreArea);
};

const emptyScore = () => {
  const scoreArea = document.querySelector('.score');
  scoreArea.innerHTML = '';
};

export const hideButtons = () => {
  hideButton('.game_btn');
  hideButton('.repeat_btn');
  hideButton('.switch');
};

export const showButtons = () => {
  showButton('.switch');
};

const failScore = () => {
  renderFailure();
  playSelector('.failure');
  setTimeout(() => {
    emptyScore();
    renderCategoryMenu();
  }, 4000);
};

const successScore = () => {
  renderSuccess();
  playSelector('.success');
  setTimeout(() => {
    emptyScore();
    renderCategoryMenu();
  }, 4000);
};

const scoreHandler = (incorrectScore) => ((incorrectScore > 0) ? failScore() : successScore());

export const startGame = () => {
  state.toggleStarted();
  showButton('.repeat_btn');
  state.gameArray = activeArray();
  const { length } = state.gameArray;
  const index = Math.floor(Math.random() * length);
  state.currentIndex = index;
  state.currentWord = state.gameArray[index];
  playAudio(state.currentWord);
};

export const nextCard = () => {
  state.gameArray = activeArray();
  const { length } = state.gameArray;
  if (length === 0) {
    scoreHandler(state.countIncorrect);
  } else {
    const index = Math.floor(Math.random() * length);
    state.currentIndex = index;
    state.currentWord = state.gameArray[index];
    setTimeout(() => { playAudio(state.currentWord); }, 1000);
  }
};

export const stopGame = () => {
  state.resetStarted();
  hideButton('.repeat_btn');
  emptyScore();
  state.resetGame();
  if (state.game && state.activeCategory !== 'main') {
    showButton('.game_btn');
  } else { hideButton('.game_btn'); }
};

export const game = (event) => {
  if (state.gameStarted && event.path[3].classList.contains('active_card')) {
    if (state.currentWord === event.path[3]) {
      event.path[3].classList.add('checked');
      event.path[3].classList.remove('active_card');
      playSelector('.correct');
      addFullStar();
      nextCard();
    } else {
      state.addIncorrect();
      addEmptyStar();
      playSelector('.error');
    }
    const starArray = domArray('.star');
    if (starArray.length > 8) {
      emptyScore();
    }
  }
};

export const removeWhiteSpaces = (string) => string.split(/\s/).join('');
