const state = {
  activeCategory: 'main',
  game: false,
  gameStarted: false,
  gameArray: [],
  currentWord: '',
  currentIndex: '0',
  countCorrect: 0,
  countIncorrect: 0,
  resetGame() {
    this.gameArray = [];
    this.currentWord = '';
    this.currentIndex = '0';
    this.countCorrect = 0;
    this.countIncorrect = 0;
  },
  addIncorrect() {
    this.countIncorrect += 1;
  },
  resetStarted() {
    this.gameStarted = false;
  },
  toggleStarted() {
    this.gameStarted = !this.gameStarted;
  },
  toggleGameState() {
    this.game = !(this.game);
  },
};

export default state;
